// src/components/Addmycommunity/index.js
import React, { PropTypes, Component } from 'react';
import classnames from 'classnames';
import Header from '../Header/Header.js';

class EditProfile extends Component {  


   constructor(props) {
    super(props);
    

    this.state = {
      value: "",     
      elements: [],
      communitydata: [],
      userdata: [],
      memberdata: [],
      successmsg:"",
      errormsg:"",
      showResults: false
    };
  }

      componentDidMount() {
        
          document.title = this.props.route.title;

          if(!sessionStorage.getItem('session_tokenid'))
          {
            this.props.router.push('/login');
          }
      }

      componentWillMount() {

          this.callApi(sessionStorage.getItem('session_tokenid'))           
          .then((userdata) => {this.setState({ userdata: userdata }) 

            console.log(userdata);

        });
      }

      callApi = async (uid) => {
     
    const response = await fetch('/api/getUserById?uid='+uid,{
      method: 'GET',   
      headers: {"pragma": "no-cache","cache-control" : "no-cache"}
    });

     const body = await response.json();

      if (response.status !== 200) throw Error(body.message);

      return body;
  }


  render() {
    const { className, ...props } = this.props;
    return (
	
      <div className={classnames('About', className)} {...props}>
	     
		 <Header />
	  
                  <div className="login-section bg-ribbins">
                  <div className="container">
                  <div className="tab-wrap">
                  <h2 className="head-title">Edit Profile</h2>

                  <form role="form">
                  <div className="input-group">
                  <span className="input-group-addon"><i className="fa fa-user"></i></span>
                  <input id="login-username" type="text" className="form-control" name="name" value={sessionStorage.getItem('session_username')} placeholder="Name" /></div>           
                  <div className="input-group">
                  <span className="input-group-addon"><i className="fa fa-location-arrow"></i></span>
                  <input id="login-password" type="text" className="form-control" name="location" placeholder="Location" />
                  </div>
                  <button className="btn button--primary" type="submit">Submit <i aria-hidden="true" className="fa fa-angle-right"></i> </button>
                  </form>        
                  </div>
                  </div>
                </div>
      </div>
    );
  }
}



export default EditProfile;


