// src/components/About/index.js
import React, { PropTypes, Component } from 'react';
import classnames from 'classnames';
import Adminheader from '../Header/Adminheader.js';
import './style.css';
import { Link } from 'react-router';

class AllCommunity extends Component {
  // static propTypes = {}
  // static defaultProps = {}
  // state = {} 

  constructor(props) {
    super(props);    

    this.state = {
      value: "",     
      elements: [],
      communitydata: [],
      userdata: [],
      successmsg:"",
      errormsg:"",
      showResults: false,
      showedit: false,
      currentLocation:""
    };  

  }

  componentWillMount() {
      
      document.title = this.props.route.title;
      //document.getElementById("footerid").remove();

      var elements = [];  
      var obj = {};

      this.callApiGetAllCommunity()
      //.then(res => res.json())
      .then((communitydata) => {this.setState({ communitydata: communitydata })

      console.log(this.state.communitydata);
      const array = communitydata.map(function(x,i){      

      elements.push({"latlong" : x.community_lat_long, "community_name" : x.community_name , "community_id" : x.community_id , "community_tagline" : x.community_tagline , "name" :x.name })
      //elements.push(x)         
      })
          //window.initialize(elements);
      });
  }

  callApiGetAllCommunity = async () => {

      const response = await fetch('/api/allcommunities',{
      method: 'GET',   
      headers: {"pragma": "no-cache","cache-control" : "no-cache"}
      });

      const body = await response.json();

      if (response.status !== 200) throw Error(body.message);

      return body;
  }

  render() {
    const { className, ...props } = this.props;
    return (
    <div id="wrapper" {...props}>  
        <Adminheader />
          <div id="page-wrapper">
    <div className="container-fluid">
      <div id="main" >
        <div className="col-sm-12 col-md-12" id="content">
          <div className="title"><h3>Community Management</h3><div className="sep"><img src="/images/sep.jpg" /></div></div>
          <div className="table-responsive">
            <table className="table table-hover">
              <thead>
                <tr>
                  <th>Community Name</th>
                  <th>Tag Line</th>
                  <th>Owner Name</th>
                  <th>Description</th>
                  <th>Detail</th>
                </tr>
              </thead>
              <tbody>

              {this.state.communitydata.map(member =>

                <tr tabindex="1">
                  <td>{member.community_name}</td>
                  <td>{member.community_tagline}</td>
                  <td>{member.name}</td>
                  <td>{member.about_you}</td>
                  <td> <a href={'members-card/'+member.community_id} className="view-detail">View details</a> </td>
                </tr>
              )}

                <tr tabindex="2">
                  <td>Mary</td>
                  <td>Moe</td>
                  <td>mary@example.com</td>
                </tr>
                <tr tabindex="3">
                  <td>July</td>
                  <td>Dooley</td>
                  <td>july@example.com</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  </div>
      </div>

    );
  }
}

export default AllCommunity;


